import { useState } from 'react';
import Multiselect from "multiselect-react-dropdown";
import api from './utils/api';
import useFetchData from "./utils/useFetchData";
import Modal from './UI/Modal';
import StatusModal from './UI/StatusModal';
import ConfirmationModal from './UI/ConfirmationModal';
import ErrorPage from './UI/ErrorPage';
import { Snackbar } from '@mui/material';
import MatrielCard from './MatrielCard';

const Matriel = () => {

  const [selectedLevels, setSelectedLevels] = useState([]);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [errorModal, setErrorModal] = useState(null);
  const [isErrorOpen, setIsErrorOpen] = useState(false);
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');

  // Form state
  const [formData, setFormData] = useState({
    name: '',
    levels:[],
  });
  const {  levels, matriels, loading, error, refetch  } = useFetchData();
  
  const handleAddService = async () => {
    try {
      await api.post('/api/matriels', formData);
      refetch();
      setFormData({
        name: '',
        levels:[],
      });
      setIsModalOpen(false);
      setSnackbarMessage('Add Matrieil Success!.');
      setSnackbarOpen(true);
    } catch (error) {
      console.error(error);
      setErrorModal('An error whene adding your service');
      setIsErrorOpen(true);
    }
  };

  const handleOpenSubmite = (event) => {
    event.preventDefault();
    setIsAddModalOpen(true);
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };
  const handleMultiselectChange = (selectedList) => {
    selectedList.map((item) => item._id);
    setSelectedLevels(selectedList);
    setFormData({
      ...formData,
      levels: selectedList,
    });
  };
  if (error){ return <ErrorPage error={error} />}
  if (loading){ return 'loading ...'}
  return (
    <div className="container mx-auto py-8 px-4">
        <div className="">
          <button
            onClick={() => setIsModalOpen(true)}
            className="fixed bottom-4 right-4 inline-flex items-center justify-center whitespace-nowrap rounded-full text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 bg-bink-600 text-white hover:bg-bink-300 h-10 pr-2 py-2"
          >
            <span className='text-xl px-2'>+</span>
            Create Service
          </button>
        </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {matriels.map((item, index) => (
          <MatrielCard 
          key={index} 
          {...item} 
          RenderComponent={refetch} 
          allLevels={levels}/>
        ))}
      </div>
      {isModalOpen && (
        <Modal onClose={() =>setIsModalOpen(false)}>
          <div className="popover-content sm:min-w-[300px] sm:max-w-[500px]">
            <div className="space-y-4">
              <div className="space-y-2">
                <h3 className="text-lg font-medium">Add New Service</h3>
              </div>
              <form className="grid gap-4" onSubmit={handleOpenSubmite}>
                <div className="grid gap-2">
                  <label htmlFor="name">Matriel Name</label>
                  <input
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    className="h-10 block w-full mb-2 border border-muted p-2 rounded"
                    placeholder="Enter service name"
                  />
                </div>
                <div className="grid gap-2">
                  <Multiselect
                    options={levels} // Options to display in the dropdown
                    selectedValues={selectedLevels} // Preselected Levels
                    onSelect={handleMultiselectChange} // Handle select
                    onRemove={handleMultiselectChange} // Handle remove
                    displayValue="name" // Property name to display in the dropdown
                    placeholder="Select levels"
                    style={{
                      multiselectContainer: { backgroundColor: "white" },
                      chips: { background: "#6366F1", color: "white" },
                      optionContainer: { backgroundColor: "#F3F4F6" },
                      option: { color: "#4B5563" },
                    }}
                  />
                </div>
                
                <button
                  type="submit"
                  className="inline-flex items-center justify-center whitespace-nowrap rounded-full text-sm text-white font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-firstColor hover:bg-secondColor hover:border-firstColor hover:text-black h-10"
                >
                  Save Service
                </button>
              </form>
            </div>
          </div>
        </Modal>
      )}
      { errorModal && (
        <StatusModal
          type="error"
          title="Error Occurredr"
          message={errorModal}
          isOpen={isErrorOpen}
          onClose={() => setIsErrorOpen(false)}
        />
         )}
      <ConfirmationModal
        isOpen={isAddModalOpen}
        onClose={() => setIsAddModalOpen(false)}
        onConfirm={handleAddService}
        title="Confirm Add"
        message="Are you sure you want to add this service?"
        confirmText="Add"
        confirmColor="#db2777"
      />
       
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        message={snackbarMessage}
      />
    </div>
  );
};

export default Matriel;