import React, { useState } from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import { X, BookOpen, Calendar, Users } from 'lucide-react';
import AddExecrise from './addExercises/addExecrise';
import Header from './UI/Header';
import Exercises from './Exercises';
import Levels from './Levels';
import Matriel from './Matriel';
import Unit from './Unit';

const navItems = [
    { name: 'Add Execrise', link: '/ExamsFrontend', icon: Calendar },
  { name: 'Exercises', link: '/ExamsFrontend/Exercises', icon: BookOpen },
  { name: 'Levels', link: '/ExamsFrontend/levels', icon: Users },
  { name: 'Matriel', link: '/ExamsFrontend/matriel', icon: Users },
  { name: 'Unit', link: '/ExamsFrontend/unit', icon: Users },
];

const Dashboard=() => {
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const toggleSidebar = () => setSidebarOpen(!sidebarOpen);

  return (
    <>
      {/* Sidebar */}
      <aside
        className={`${
          sidebarOpen ? 'translate-x-0' : '-translate-x-full'
        } fixed inset-y-0 left-0 z-50 w-64 bg-indigo-700 text-white transition-transform duration-300 ease-in-out lg:translate-x-0`}
      >
        <div className="flex h-16 items-center justify-between px-4 border-b border-indigo-600">
          <h2 className="text-xl font-bold">EduHub</h2>
          <button onClick={toggleSidebar} className="p-2 rounded-md hover:bg-indigo-600 lg:hidden">
            <X className="h-6 w-6" />
          </button>
        </div>
        <nav className="mt-8 px-4">
          <ul className="space-y-2">
            {navItems.map((item) => (
              <li key={item.name}>
                <Link
                  to={item.link}
                  className="flex items-center px-4 py-2 text-sm rounded-md hover:bg-indigo-600 transition-colors duration-200"
                >
                  <item.icon className="mr-3 h-5 w-5" />
                  {item.name}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </aside>
      <div className="lg:ml-64">
        <Header toggleSidebar={toggleSidebar}/>
        <Routes>
          <Route path="/ExamsFrontend" element={<AddExecrise />} />
          <Route path="/ExamsFrontend/Exercises" element={<Exercises />} />
          <Route path="/ExamsFrontend/levels" element={<Levels />} />
          <Route path="/ExamsFrontend/matriel" element={<Matriel />} />
          <Route path="/ExamsFrontend/unit" element={<Unit />} />
        </Routes>
      </div>
    </>
  );
}
export default  Dashboard;