import React, { createContext, useContext, useState, useEffect } from 'react';
import useFetchData from '../utils/useFetchData';
// Import Data to use in useState
import Trimesters from '../../Data/Trimesters';
import api from '../utils/api';

// Create the context for form data
const FormContext = createContext();

// Custom hook to use form data context
export const useFormContext = () => {
  return useContext(FormContext);
};

// FormProvider to manage form data
export const FormProvider = ({ children }) => {
  const [formData, setFormData] = useState({
    title:'just test title',
    level: '',
    Trimester: Trimesters[0],
    material: '',
    unit: '',
    difficulty: '1',
  });

  // sections state moved here
  const [sections, setSections] = useState([
    {
      content: '',
      questions: [{ question: '', answer: '' }],
      image: null,
      imagePosition: 'left',
    },
  ]);

  // fetchs the data from APIs
  const {  
     levels,
     loading, 
     error,
     setSelectedLevel,
     setSelectedMatriel,
     filteredMatriels,
     filteredUnits  } = useFetchData();

  // set the visrt value in inputs
  useEffect(() => {
      setFormData((prevData) => ({
        ...prevData,
        level: levels[0]?._id || '',
        material: filteredMatriels[0]?._id || '',
        unit: filteredUnits[0]?._id || '',
      }));
  }, [levels, filteredMatriels, filteredUnits]);

  const handleChange = (field, value) => {
    setFormData((prevData) => ({
      ...prevData,
      [field]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const exerciseData = {
        title: formData.title,
        exercise: sections,
        level: formData.level,
        Trimester: formData.Trimester,
        difficulty: formData.difficulty,
        material: formData.material,
        unit: formData.unit,
      };

      // Post the form data to the backend API
      const response = await api.post('/api/exercises', exerciseData);

      // Handle success response
      console.log('Exercise added successfully:', response.data);
    } catch (error) {
      // Handle error response
      console.error('Error adding exercise:', error);
    }
  };

  return (
    <FormContext.Provider value={{ 
      formData, 
      handleChange, 
      sections, 
      setSections, 
      handleSubmit,
      levels,
      loading, 
      error,
      setSelectedLevel,
      setSelectedMatriel,
      filteredMatriels,
      filteredUnits }
      }>
      {children}
    </FormContext.Provider>
  );
};