import React from 'react';

const ExerciseCard = ({exercise}) => {

  const positionClassesMap = {
    'left': 'flex-row',
    'right': 'flex-row-reverse',
    'top': 'flex-col-reverse',
    'bottom': 'flex-col',
  };
    return (
      <>
        <div className="exercise-container max-w-[850px] mx-auto px-8 pt-8 mb-4 bg-white border border-gray-300 rounded-lg shadow-lg">
            <div key={exercise._id} className="exercise-section mb-10">
              {/* Title and metadata */}
              <h1 className="text-2xl font-semibold text-center mb-4">{exercise.title}</h1>
    
              {/* Exercise content and questions */}
              {exercise.exercise.map((item, itemIndex) => (
                <div className={`flex ${positionClassesMap[item.imagePosition] || ''}`}>
                <div key={itemIndex} className="exercise-content mb-3 ">
                  {/* Exercise content */}
                  <p className="content text-black text-base mt-3 mb-4">{item.content}</p>
                  
                  {/* Questions */}
                  <ol className="list-decimal ml-5 space-y-3 text-base text-black leading-relaxed">
                    {item.questions.map((q, qIndex) => (
                      <li key={qIndex} className="mb-2">{q.question}</li>
                    ))}
                  </ol>
                </div>
                {item.image && (
                    <div className={`w-2/3 mx-auto`}>
                      <img
                        src={item.image}
                        alt="Dynamic"
                      />
                    </div>
                  )}
                </div>
              ))}
            </div>
            <p className="text-lg text-black mb-2">
            Level: {exercise.level} |
            material: {exercise.material} 
          </p>
          <p className="text-lg text-black mb-4">
          Trimester: {exercise.Trimester} |
          unit: {exercise.unit} |
          difficulty: {exercise.difficulty}
          </p>
        </div>
        </>
      );
};

export default ExerciseCard;